body {
	color: $white;
	@include f('ll', 16);
	@include font-smoothing;
	line-height: 1.4;
	background: $bodyBg; }

.container {
	@include clr; }

.inner {
	position: relative;
	padding: 0 10px;
	margin: 0 auto;
	max-width: 1140px;
	@include clr;
	@include md {
		max-width: 920px; }
	@include sm {
		max-width: 590px; } }

h1, h2, h3, h4, h5, h6, strong, em, i, cite, th {
	font-weight: normal;
	font-style: normal; }

a {
	text-decoration: none;
	color: inherit; }
