.header {
	padding-top: 50px; }

.btn-nav {
	position: relative;
	display: none;
	vertical-align: top;
	padding: 5px 10px 5px 40px;
	background: none;
	@include sm {
		display: inline-block; }
	@include hover {
		.btn-nav {
			&__icon {
				&:before,
				&:after,
				i {
					border-color: $yellow; }
				&:before {
					transform: translate3d(0, -3px, 0); }
				&:after {
					transform: translate3d(0, 3px, 0); } }
			&__text {
				color: $yellow; } } }
	&.is-active {
		@include hover {
			.btn-nav {
				&__icon {
					&:before {
						transform: translate3d(4px, -3px, 0) rotate(45deg); }
					&:after {
						transform: translate3d(4px, 3px, 0) rotate(-45deg); } } } }
		.btn-nav {
			&__icon {
				&:before {
					transform: translate3d(4px, -3px, 0) rotate(45deg); }
				&:after {
					transform: translate3d(4px, 3px, 0) rotate(-45deg); }
				i {
					transform: scaleX(0); } } } }
	&__icon {
		position: absolute;
		top: 16px;
		left: 0;
		height: 15px;
		&:before,
		&:after,
		i {
			position: absolute;
			left: 0;
			width: 28px;
			border-bottom: 1px solid $white;
			transition: transform 0.175s ease, border-color 0.25s ease; }
		&:before,
		&:after {
			content: "";
			@include gpu; }
		&:before {
			top: 0;
			transform-origin: top left; }
		&:after {
			bottom: 0;
			transform-origin: bottom left; }
		i {
			top: 7px; } }
	&__text {
		display: block;
		color: $white;
		@include f('lt', 18, 40);
		text-transform: uppercase;
		@include t(color); } }

.menu {
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	right: 0;

	color: $white;
	background: $gray;

	@include f('ll', 18, 40);
	text-transform: uppercase;
	a {
		color: inherit; }
	&__link {
		float: right;
		display: none;
		padding: 5px 0;
		margin-right: 140px;
		@include t(color);
		@include sm {
			display: block; }
		@include xs {
			margin-right: 0; }
		@include hover {
			color: $yellow; } } }

.nav {
	@include sm {
		position: fixed;
		top: 50px;
		left: 0;
		bottom: 0;
		padding: 30px 0 20px;
		width: 90%;
		max-width: 300px;
		background: $gray;
		transform: translate3d(-100%, 0, 0);
		transition: transform 0.25s ease; }
	&.is-active {
		@include sm {
			@include gpu; } }
	&__list {
		font-size: 0.1px;
		text-align: justify;
		&:after {
			content: "";
			display: inline-block;
			width: 100%; }
		li,
		a {
			display: inline-block;
			vertical-align: top;
			@include sm {
				display: block;
				padding: 0 20px; } }
		a {
			padding: 5px 0;
			font-size: 18px;
			@include t(color);
			@include hover {
				color: $yellow; } } } }
